import React from 'react';



export default class SectionDescriptionBody extends React.PureComponent{

    render(){
        const bClass='section-description';
        const {gfsm,czsm,cclj,tbzy}=this.props;
        const pArr=cclj?cclj.split('<br>'):[''];
        // console.log(pArr)
        return(
                <div className={bClass}>
                    <span></span><p>{gfsm}</p>
                    <span></span><p>{czsm}</p>
                    {/*<span></span><p dangerouslySetInnerHTML={{__html: cclj}}/>*/}
                    <span></span>{pArr.map((e,i)=><p key={i} dangerouslySetInnerHTML={{__html: e}}/>)}
                    <span></span><p dangerouslySetInnerHTML={{__html: tbzy}}/>
                </div>
        )
    }
}



