import React from 'react';



export default class SectionDescriptionTitle extends React.PureComponent{

    render(){
        const tClass='section-title';
        const {title}=this.props;
        return(
            <div className={tClass}>
                <span></span>
                <p>
                {title}
                {this.props.children}
                </p>
            </div>
        )
    }
}



